import React, { useEffect, createRef } from 'react';
import AniLink from 'gatsby-plugin-transition-link/AniLink'; // change to TransitionLink
import SVGLogo from './svgLogo';
import normalizeWheel from './normalizeWheel';

const checkWindow = typeof window === `undefined` ? false : true; // gatsby cannot have window called while building the pages

const duration = 400;
const isSafari = checkWindow ? /^((?!chrome|android).)*safari/i.test(navigator.userAgent) : false;

const pageChangeTransition = 0.3;

let navHidden = false;
let scrolling = false;
let windowH = checkWindow ? window.innerHeight : 0;
let scrollThreshold = -5;

// even with the normaliseWheel, Safari seems to give different values to other browsers
if (isSafari) {
  scrollThreshold = 0;
}

// TODO - gets stuck occasionally
export default () => {
  const navEl = createRef();
  useEffect(() => {
    console.log("1",1);

    if (navEl.current.classList.contains('-transitionHide')) {
      navHidden = true;
      navEl.current.classList.add('-hidden');
    }

    // use memo?
    const NavSlideScroll = e => {
      if (!scrolling) {
        window.requestAnimationFrame(() => {
          const top = window.pageYOffset;

          if (!navHidden) {
            const normaliseWheelY = normalizeWheel(e);

            if (normaliseWheelY > 0 && top > 10) {
              e.preventDefault();

              navHidden = true;
              scrolling = true;

              setTimeout(() => {
                scrolling = false;
              }, duration);

              navEl.current.style.opacity = 0;
              navEl.current.classList.add('-hidden');

              // window.scroll({
              //   top: 0,
              //   behavior: "smooth"
              // })
            }
          } else if (top < windowH * 0.1) {
            const normaliseWheelY = normalizeWheel(e);

            // scrollThreshold to prevent accidental nav reveals
            if (normaliseWheelY < scrollThreshold && navHidden) {
              e.preventDefault();

              navHidden = false;
              scrolling = true;

              setTimeout(() => {
                scrolling = false;
              }, duration);

              navEl.current.style.opacity = 1;
              navEl.current.classList.remove('-hidden');
              navEl.current.classList.remove('-transitionHide');

              window.scroll({
                top: 0,
                behavior: 'smooth',
              });
            }
          }
        });
      }
    };

    if (checkWindow) {
      window.addEventListener('wheel', NavSlideScroll, { passive: true });

      return () => {
        window.removeEventListener('wheel', NavSlideScroll);
      };
    }
  });

  const links = ['work', 'about', 'contact'];

  return (
    <nav className="nav" ref={navEl}>
      <div className="nav__drop-shadow" />

      <div className="nav__contain">
        <AniLink fade duration={pageChangeTransition} to="/">
          <SVGLogo />
        </AniLink>

        <div className="nav__links">
          {links.map(link => (
            <AniLink
              fade
              duration={pageChangeTransition}
              key={`navLink-${link}`}
              className="nav__link"
              activeClassName="-active"
              partiallyActive={true}
              to={'/' + link}
            >
              <p className="nav__link-text fontSize-p2">{link}</p>
            </AniLink>
          ))}
        </div>
      </div>
    </nav>
  );
};
