import React from 'react';
import { TimelineLite, Power2 } from 'gsap';

export default class CursorOverlay extends React.PureComponent {
  cursorOverlay = React.createRef();
  cursorOverlayCursor = React.createRef();

  componentDidMount() {
    this.init();
  }

  componentDidUpdate(){
    this.init();
  }

  init(){
    this.cursorDimensions = this.cursorOverlay.current.getBoundingClientRect();

    // TODO - doesn't appear in live build
    console.log('this.props.items',this.props.items);
    
    for (const hoverItem of this.props.items) {
      console.log("hoverItem",hoverItem);

      hoverItem.addEventListener('mouseenter', this.onMouseEnter.bind(this));
      hoverItem.addEventListener('mouseleave', this.onMouseLeave.bind(this));
      hoverItem.addEventListener('mousemove', this.onMouseMove.bind(this));
      hoverItem.addEventListener('dragstart', this.onDragstart.bind(this));

      if(!hoverItem.classList.contains('-noClick')){
        hoverItem.addEventListener('click', this.onClick.bind(this));
      }
    }
  }

  componentWillUnmount() {
    for (const hoverItem of this.props.items) {
      hoverItem.removeEventListener('mouseenter', this.onMouseEnter);
      hoverItem.removeEventListener('mouseleave', this.onMouseLeave);
      hoverItem.removeEventListener('mousemove', this.onMouseMove);
      hoverItem.removeEventListener('dragstart', this.onDragstart);

      if(!hoverItem.classList.contains('-noClick')){
        hoverItem.removeEventListener('click', this.onClick);
      }
    }
  }

  onMouseEnter() {
    this.cursorOverlay.current.classList.remove('-hidden');
    this.cursorOverlayCursor.current.style.transform = 'scale(0.4)';
  }

  onMouseLeave() {
    if (this.cursorOverlay.current === null) return;
    this.cursorOverlay.current.classList.add('-hidden');
    this.cursorOverlayCursor.current.style.transform = 'scale(0)';
  }

  onMouseMove(e) {
    // console.log('e',e);
    if (this.cursorOverlay.current === null) return;
    let x = e.clientX - this.cursorDimensions.width / 2;
    let y = e.clientY - this.cursorDimensions.height / 2;

    this.cursorOverlay.current.style.transform = `translate(${x}px, ${y}px)`;
  }

  onClick() {
    const rippleTime = 0.4;
    const easing = Power2.easeOut;

    let tl = new TimelineLite();

    // TODO - timeline might be slow, consider adding requestAnimations
    tl.set(this.cursorOverlayCursor.current, {
      scale: 0.1,
      opacity: 0,
      transition: 'none',
    })
      .set(this.cursorOverlay.current, { transition: 'none' })
      .to(this.cursorOverlayCursor.current, rippleTime * 0.3, {
        scale: 1,
        opacity: 0.5,
        ease: easing,
      })
      .to(this.cursorOverlayCursor.current, rippleTime * 0.2, {
        scale: 1,
        opacity: 0.7,
        ease: easing,
      })
      .to(this.cursorOverlayCursor.current, rippleTime * 0.2, {
        scale: 0.1,
        opacity: 0,
        ease: easing,
      })
      .add(() => {
        this.cursorOverlayCursor.current.removeAttribute('style');
        this.cursorOverlay.current.removeAttribute('style');
        this.cursorOverlay.current.classList.add('-hidden');
      }); // reset after timline styles
  }

  onDragstart() {
    return false;
  }

  render() {
    return (
      <div ref={this.cursorOverlay} className="cursorOverlay -hidden">
        <div
          ref={this.cursorOverlayCursor}
          className="cursorOverlay-cursor"
          style={{ transform: `scale(0) translate(-50%, -50%)` }}
        />
      </div>
    );
  }
}
